@import url(https://fonts.googleapis.com/css?family-Poppins:400,500,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{padding:0;margin:0;font-family:Poppins, sans-serif;color:#dfe3e8;background-image:linear-gradient(to bottom, rgba(149,164,174,0.968627), rgba(149,164,174,0.968627)),url(/wip-website/static/media/dark_background.648284dc.jpg)}.p-indent{text-indent:30px}.title{font-weight:500;font-size:3em;padding:20px;display:block}.landing-wrapper{padding-top:6rem;padding-bottom:calc(6rem + 74px);display:flex;align-items:center;height:100vh;background-image:linear-gradient(to bottom, rgba(149,164,174,0.968627) 10%, rgba(22,51,85,0.968627) 90%),url(/wip-website/static/media/dark_background.648284dc.jpg)}.landing-wrapper .text-name{padding-left:10vw;font-size:calc(38px + (68 - 38) * ((100vw - 600px) / (1600 - 600)));padding-bottom:5vh}.landing-wrapper .text-title{padding-left:10vw;font-size:calc(32px + (56 - 32) * ((100vw - 600px) / (1600 - 600)))}.about-wrapper{padding-bottom:30px;background-image:linear-gradient(to bottom, rgba(22,51,85,0.968627), rgba(166,175,186,0.968627)),url(/wip-website/static/media/dark_background.648284dc.jpg)}.about-image{position:relative;display:block;max-width:100%;object-fit:cover;border-radius:1rem;border:3px solid #021a40;overflow:hidden;background-color:rgba(26,188,156,0.9);margin:20px auto}.about-text{max-width:100%;max-height:100%;position:relative;top:50%;transform:translateY(-50%)}.timeline-wrapper{padding-bottom:30px;color:#132d48;background-image:linear-gradient(to bottom, rgba(166,175,186,0.960784), rgba(223,227,232,0.960784)),url(/wip-website/static/media/dark_background.648284dc.jpg)}.interests-wrapper{background-image:linear-gradient(to bottom, rgba(223,227,232,0.968627), rgba(42,66,92,0.968627)),url(/wip-website/static/media/dark_background.648284dc.jpg);padding-bottom:30px}.contact-wrapper{background-image:linear-gradient(to bottom, rgba(42,66,92,0.968627), rgba(25,59,98,0.968627)),url(/wip-website/static/media/dark_background.648284dc.jpg);padding-bottom:30px}

.btn-preclick{background:none;border:none}.btn-postclick{background:none;border:none}.btn-preclick:hover,.btn-postclick:hover{color:#fff;background:none}.btn-postclick:focus,.btn-postclick.focus{color:#fff;background:none;box-shadow:0 0 0 0.2rem rgba(60,198,171,0.5)}.form-custom:focus{border-color:white;color:#495057;background-color:#fff;outline:0;box-shadow:0 0 0 0.2rem rgba(26,188,156,0.25)}

.timeline-item{margin:20px 0px}.timeline-image-parent{height:280px;width:280px;object-fit:contain;border-radius:1rem;border:3px solid #021a40;background-color:white;margin:0px 20px}.timeline-image{position:relative;display:block;width:95%;height:95%;overflow:hidden;object-fit:contain;background-position:center;margin-left:auto;margin-right:auto}h4{font-size:1.2em}.timeline-text{padding:20px 0px}

.interest-item{cursor:pointer;position:relative;display:block;max-width:25rem;border-radius:0.5rem;overflow:hidden}.interest-item .interest-item-caption{position:absolute;top:0;left:0;transition:all 0.4s ease-in-out;opacity:0;background-color:black}.interest-item-caption{padding:10px}.interest-item .interest-item-caption:hover{opacity:0.8}.interest-item .interest-item-caption .interest-item-caption-content{font-size:1.5rem}.interest-title{margin-top:20px;display:block}

.navbar-open{border:1px solid white}.navbar-close{border:none}.custom-nav-link{color:gray}.navbar-color{background-color:rgba(255,255,255,0)}

@-webkit-keyframes slideInFromBottom{0%{transform:translateY(100%);visibility:visible}100%{transform:translateY(0);visibility:visible}}@keyframes slideInFromBottom{0%{transform:translateY(100%);visibility:visible}100%{transform:translateY(0);visibility:visible}}.sidebar{position:fixed;height:100%;width:11vh;z-index:1;top:0;padding-left:5vh;display:block;text-align:center;visibility:hidden}.sidebar .icon{height:6vh;width:6vh;margin-bottom:2.5vh}.sidebar .icon1{-webkit-animation:1s ease-out 1s 1 forwards slideInFromBottom;animation:1s ease-out 1s 1 forwards slideInFromBottom}.sidebar .icon2{-webkit-animation:1s ease-out 1.25s 1 forwards slideInFromBottom;animation:1s ease-out 1.25s 1 forwards slideInFromBottom}.sidebar .icon3{-webkit-animation:1s ease-out 1.5s 1 forwards slideInFromBottom;animation:1s ease-out 1.5s 1 forwards slideInFromBottom}.sidebar .icon4{-webkit-animation:1s ease-out 1.75s 1 forwards slideInFromBottom;animation:1s ease-out 1.75s 1 forwards slideInFromBottom}.sidebar>a:hover{opacity:0.7;transition:all 0.3s ease}.line-top{visibility:hidden;-webkit-animation:1s ease-out 0s 1 forwards slideInFromBottom;animation:1s ease-out 0s 1 forwards slideInFromBottom;height:55.5vh;margin-bottom:2.5vh}.line-bottom{visibility:hidden;-webkit-animation:1s ease-out 2s 1 forwards slideInFromBottom;animation:1s ease-out 2s 1 forwards slideInFromBottom;height:8vh}

